.SearchBarContainer {
  min-height: 56px;
  min-width: 480px;
  background-color: white;
  box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.0532439);
  border-radius: 5px;
  display: flex;
  align-items: center;
  color: #848484;
}

.fa-magnifying-glass {
  width: 18px;
  height: 18px;
  padding-left: 32px;
  padding-right: 24px;
}

.SearchBarContainer input {
  width: 100%;
  height: 50px;
  border: none;
  font: inherit;
  font-size: 14px;
  text-indent: 5px;
  margin-right: 5px;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

@media screen and (max-width: 790px) {
  .SearchBarContainer {
    max-width: 100%;
  }
}

.app.dark-mode > .filters > * {
  color: #fff;
  background-color: #2b3844;
}

.app.dark-mode > .filters > * > * {
  color: #fff;
  background-color: #2b3844;
}
